  <div class="popup" v-if="feedbackMessage != '' || txInProgress">
    <div
      class="popupX"
      @click="
        feedbackMessage = '';
        txInProgress = false;
      "
    >
      x
    </div>
    <h4 class="animate-popup" v-if="feedbackMessage != ''">
