            >
              withdraw BCH
            </button>
          </div>

          <br />

          <h4>Reaper Mint Price</h4>
          <p>
            {{ Math.round(reaperBchPerNftE0 / 10 ** 12) / 10 ** 6 }}
            BCH
          </p>
          <input
            id="reaperBchPerNftE0"
            type="text"
            class="form-control mx-auto"
            v-model="reaperBchPerNftE0"
          />
          <div>
            <button
              class="btn btn-std"
              :disabled="!isAdmin"
              type="button"
              @click="setReaperBchPerNftE0"
            >
              set new price
              {{ Math.round(reaperBchPerNftE0 / 10 ** 12) / 10 ** 6 }} BCH
            </button>
          </div>

          <br />

          <h2 class="mb-0">Pause Reaper Mint</h2>
          Mint is active: {{ !reaperMintPaused }}
          <p>
            <button
              @click="setReaperMintPaused(true)"
              :disabled="reaperMintPaused || !isAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setReaperMintPaused(false)"
              :disabled="!reaperMintPaused || !isAdmin"
              class="btn btn-success"
            >
              Unpause
            </button>
          </p>

          <h2 class="mb-0">Pause Reaper Staking</h2>
          Staking is active: {{ !reaperStakingPaused }}
          <p>
            <button
              @click="setReaperStakingPaused(true)"
              :disabled="reaperStakingPaused || !isAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setReaperStakingPaused(false)"
              :disabled="!reaperStakingPaused || !isAdmin"
              class="btn btn-success"
            >
              Unpause
