        </div>
      </div>
    </div>
  </div>
  <div class="popup" v-if="feedbackMessage != ''">
    <h4 v-if="feedbackMessage != ''">{{ feedbackMessage }}</h4>
  </div>
</template>

<script>
import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import Cards from "../../artifacts/contracts/Cards.sol/Cards.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      contracts: {},

      reaperBchPerNftE0: "0",

      newMinterAddress: "",

      feedbackMessage: "",

      bchBalanceE0: {},
      rewardTokenMintAllowanceE0: {},
      rewardTokenBalanceE0: {},

      isAdmin: false,

      reaperMintPaused: false,
      reaperStakingPaused: false,

      totalStakedPower: 0,
      baseStakingPower: 0,
      rewardTokenEmissionPerHourE18: 0,
    };
  },
  methods: {
    mintRewardToken: async function (newMinterAddress, amount) {
      let tx = await this.contracts["svc"].mint(
        newMinterAddress,
        ethers.utils.parseUnits(amount.toString(), "ether")
      );
      await tx.wait();
    },

    setReaperMintPaused: async function (newPaused) {
      let tx = await this.contracts["reaper"].setMintPaused(newPaused);
      await tx.wait();
      window.location.reload();
    },

    setReaperStakingPaused: async function (newPaused) {
      let tx = await this.contracts["reaper"].setStakingPaused(newPaused);
      await tx.wait();
      window.location.reload();
    },

    setReaperBchPerNftE0: async function () {
      let tx = await this.contracts["reaper"].setBchPerNft(
        String(this.reaperBchPerNftE0)
      );
      await tx.wait();
      window.location.reload();
    },

    setMintAllowance: async function (_address, _newAllowance) {
      this.feedbackMessage = "";

      if (_newAllowance === 0) {
        let tx = await this.contracts["svc"].removeMinter(_address);
        await tx.wait();
        window.location.reload();
      } else {
        let tx = await this.contracts["svc"].updateMintersAllowance(
          _address,
          _newAllowance
        );
        await tx.wait();
        window.location.reload();
      }
    },

    getContractAddress: function (_contractName) {
      let contractAddress;
      switch (_contractName) {
        case "newMinter":
          contractAddress = this.newMinterAddress;
          break;
        default:
          contractAddress = window.ca[_contractName];
          break;
      }
      return contractAddress;
    },

    getContract: function (_contractName) {
      let contract;
      switch (_contractName) {
        case "reaper":
          contract = this.contracts["reaper"];
          break;
        case "cards":
          contract = this.contracts["cards"];
          break;
        default:
          this.feedbackMessage = "Contract not implemented";
          return null;
      }
      return contract;
    },

    withdrawBchFromContract: async function (_contractName) {
      let _contract = this.getContract(_contractName);
      if (_contract === null) {
        return false;
      }
      let tx = await _contract.withdrawBch();
      await tx.wait();
      window.location.reload();
    },

    resetContract: async function (_contractName) {
      let _contract = this.getContract(_contractName);
      if (_contract === null) {
        return false;
      }
      let tx = await _contract.reset();
      await tx.wait();
      window.location.reload();
    },

    setNewMintAllowance: async function (_addressName, _newAllowance) {
      let _contractAddress = this.getContractAddress(_addressName);
      if (_contractAddress === null) {
        return false;
      }

      this.setMintAllowance(_contractAddress, _newAllowance);
    },

    getRewardTokenMintAllowance: async function (_addressName) {
      let _address = this.getContractAddress(_addressName);
      if (_address === null) {
        return false;
      }

      this.contracts["svc"]
        .remainingMintersAllowance(_address)
        .then((_allowance) => {
          this.rewardTokenMintAllowanceE0[_addressName] = String(_allowance);
        });
    },

    getRewardTokenBalance: async function (_addressName) {
      let _address = this.getContractAddress(_addressName);
      if (_address === null) {
        return false;
      }

      this.contracts["svc"].balanceOf(_address).then((_balance) => {
        this.rewardTokenBalanceE0[_addressName] = String(_balance);
      });
    },

    getBchBalance: async function (_addressName) {
      let _address = this.getContractAddress(_addressName);
      if (_address === null) {
        return false;
      }

      this.provider.getBalance(_address).then((balance) => {
        this.bchBalanceE0[_addressName] = String(balance);
      });
    },
  },

  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        this.contracts["svc"] = new ethers.Contract(
          window.ca["svc"],
          RewardToken.abi,
          this.signer
        );
        this.contracts["reaper"] = new ethers.Contract(
          window.ca["reaper"],
          Reaperville.abi,
          this.signer
        );
        this.contracts["cards"] = new ethers.Contract(
          window.ca["cards"],
          Cards.abi,
          this.signer
        );
        // this.flexUsdTokenContract = new ethers.Contract(
        //   window.FlexUsdAddress,
        //   RewardToken.abi,
        //   this.signer
        // );
      })
      .then(() => {
        this.signer.getAddress().then((address) => {
          this.newMinterAddress = address;
          this.getRewardTokenMintAllowance("newMinter");
          if (address === window.ca["dev"]) {
            this.isAdmin = true;
          }
        });
        this.contracts["reaper"]
          .showStakerInfo(window.ca["dev"])
          .then((stakerInfo) => {
            this.totalStakedPower = stakerInfo[2];
            this.baseStakingPower = stakerInfo[3];
            this.rewardTokenEmissionPerHourE18 = String(
              Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
                100000
            );
          });

        const _contractsToLoad = ["reaper", "cards"];
        for (let _contract of _contractsToLoad) {
          this.getBchBalance(_contract);
          this.getRewardTokenBalance(_contract);
          this.getRewardTokenMintAllowance(_contract);
        }

        this.contracts["reaper"].isMintPausedStakingPaused().then((result) => {
          this.reaperMintPaused = result[0];
          this.reaperStakingPaused = result[1];
        });
        this.contracts["reaper"].bchPerNft().then((_bchPerNft) => {
          this.reaperBchPerNftE0 = String(_bchPerNft);
        });
      });
  },
};
</script>
