<template>
  <section
    class="text-center container col-lg-8 col-md-9 col-sm-10 mx-auto mt-2 mb-4"
  >
    <h1 class="animate-characters">The Surviville Card Game</h1>

    <template v-if="showSummary === false">
      <table class="mx-auto my-4">
        <tr>
          <td v-for="idx in 3" :key="idx">
            <img
              class="button"
              @click="uncoverField(idx - 1)"
              :src="imgArr[idx - 1]"
            />
          </td>
        </tr>
        <tr>
          <td v-for="idx2 in 3" :key="idx2">
            <img
              class="button"
              @click="uncoverField(idx2 + 2)"
              :src="imgArr[idx2 + 2]"
            />
          </td>
        </tr>
        <tr>
          <td v-for="idx3 in 3" :key="idx3">
            <img
              class="button"
              @click="uncoverField(idx3 + 5)"
              :src="imgArr[idx3 + 5]"
            />
          </td>
        </tr>
      </table>

      <h1 class="mt-4">Card {{ currentCard }} of {{ cards2Play }}</h1>

      <span class="row-lg-12">
        <span class="column">
          <div class="col-lg-6 col-md-8 mx-auto">
            <button
              v-if="uncoveredWinningFields < 3"
              class="btn btn-std my-2 btn-large"
              @click="uncoverAllFields"
            >
              Uncover all fields
            </button>
            <button
              v-else-if="currentCard < cards2Play"
              class="btn btn-std my-2 btn-large"
              @click="showNextCard"
            >
              Show next card
            </button>
            <button
              v-else
              class="btn btn-std my-2 btn-large"
              @click="showNextCard"
            >
              Play again
            </button>
            <!-- <template
              v-if="currentCard >= cards2Play && uncoveredWinningFields >= 3"
            > -->
            &nbsp;
            <button
              class="btn btn-std my-2 btn-large"
              @click="
                showSummary = true;
                currentCard = cards2Play;
              "
            >
              Show Summary
            </button>
            <!-- </template> -->
          </div>
        </span>
      </span>

      <div class="row">
        <h3 class="text-monospace col-lg-12 col-md-12 mx-auto">
          {{ feedbackMessage }}
        </h3>
      </div>
    </template>

    <template v-if="showSummary === true">
      <table class="mx-auto my-4">
        <tr
          v-for="idx in winningsArr.length"
          :key="idx"
          class="text-muted lead"
        >
          {{
            idx
          }}. card won
          {{
            winningsArr[idx - 1]
          }}
          SVC
        </tr>

        <hr class="my-3" />

        <tr class="text-muted lead">
          Total won:
          {{
            tokensWon
          }}
        </tr>
      </table>

      <button class="btn btn-std my-2 btn-large" @click="showNextCard">
        Play again
      </button>
    </template>

    <!-- <button class="btn btn-std my-2 btn-large" @click="simulateGame">
      Simulate Game
    </button>
    <br /> -->
  </section>
</template>

<script>
const payouts = [5000, 2500, 1000, 500, 250, 200, 150, 125, 0];

export default {
  name: "App",
  props: ["cards2Play", "tokensWon", "gameStats"],
  emits: ["cardsPlayed"],

  data() {
    return {
      currentCard: 1,
      winningsArr: [],

      imgArr: [],
      uncoveredImgArr: [],
      uncoveredWinningFields: 0,

      showSummary: false,

      feedbackMessage: "",
    };
  },

  methods: {
    shuffle: function (array) {
      let currentIndex = array.length;
      let randomIndex;

      // While there remain elements to shuffle.
      while (currentIndex != 0) {
        // Pick a remaining element.
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }

      return array;
    },

    showNextCard: function () {
      this.currentCard++;
      if (this.currentCard > this.cards2Play) {
        this.$emit("cardsPlayed");
      } else {
        this.showCard(this.currentCard);
      }
    },

    uncoverField: function (_fieldNr) {
      let _uncoveredField =
        "images/cards/" + this.uncoveredImgArr[_fieldNr] + ".png";
      if (this.imgArr[_fieldNr] === _uncoveredField) {
        return false;
      }
      this.imgArr[_fieldNr] = _uncoveredField;

      console.log(
        this.uncoveredImgArr[_fieldNr],
        this.winningsArr[this.currentCard - 1]
      );

      if (
        Number(this.uncoveredImgArr[_fieldNr]) ===
        Number(this.winningsArr[this.currentCard - 1])
      ) {
        this.uncoveredWinningFields += 1;
      }

      if (this.uncoveredWinningFields >= 3) {
        this.feedbackMessage =
          "This card wins " + this.winningsArr[this.currentCard - 1] + " SVC";
      }
    },

    uncoverAllFields: function () {
      for (let i = 0; i < 9; ++i) {
        this.uncoverField(i);
      }
    },

    showCard: function (_card) {
      console.log(_card);
      const cardWins = this.winningsArr[_card - 1];
      console.log("cardWins", cardWins);

      // resetting card display
      this.feedbackMessage = "";
      this.uncoveredWinningFields = 0;
      this.imgArr = [];
      this.uncoveredImgArr = [];
      for (let i = 0; i < 9; ++i) {
        this.imgArr.push("images/cards/svc.png");
      }

      let fieldNumbers = [];
      let nonwinningNumbers = [];
      // creating card
      // nonwinning numbers
      for (let i = 0; i < payouts.length; ++i) {
        let payout = payouts[i];
        if (Number(payout) === Number(cardWins)) {
          continue;
        }
        for (let j = 1; j <= 2; ++j) {
          nonwinningNumbers.push(payout);
        }
      }
      console.log("nonwinningNumbers", nonwinningNumbers);
      this.shuffle(nonwinningNumbers);
      fieldNumbers = nonwinningNumbers.slice(0, 6);
      console.log(fieldNumbers);
      // winning numbers
      for (let i = 1; i <= 3; ++i) {
        fieldNumbers.push(cardWins);
      }
      this.shuffle(fieldNumbers);

      this.uncoveredImgArr = this.shuffle(fieldNumbers);
    },

    buildWinningsArr: function (w, _cards2Play) {
      console.log(w);
      let _winningsArr = [];
      for (const [key, value] of Object.entries(w)) {
        console.log(`${key}: ${value}`);
        for (let i = 0; i < value; ++i) {
          _winningsArr.push(key);
        }
      }
      for (let i = _winningsArr.length; i < _cards2Play; ++i) {
        _winningsArr.push(0);
      }
      console.log("_winningsArr before shuffle", _winningsArr);

      this.winningsArr = this.shuffle(_winningsArr);
      console.log("winningsArr after shuffle", this.winningsArr);
      this.showCard(1);
      // for (let i = 0; i < winningsArr.length; ++i) {
      //   this.playCard(winningsArr[i]);
      // }
    },

    simulateGame: function () {
      let _cards2Play = 50;
      let _tokensWon = 9500;
      let _gameStats = 2073862;

      this.extractGameInfo(_cards2Play, _tokensWon, _gameStats);
    },

    extractGameInfo: function (_cards2Play, _tokensWon, _gameStats) {
      console.log("PlayCards:extractGameInfo");
      console.log("You played", _cards2Play, "cards");

      let returnString = "";
      if (_tokensWon <= 0) {
        returnString =
          "Unfortunately, you did not win anything! Better luck next time!";
        return returnString;
      } else {
        returnString = "Congratulations! You won " + _tokensWon + " SVC. \n\n";
      }

      const modulos = [2057701, 542351, 111701, 16151, 1351, 51, 1];
      const payoutsWithModulos = [5000, 2500, 1000, 500, 250, 200, 150];
      let w = {};
      for (let i = 0; i < modulos.length; ++i) {
        w[payoutsWithModulos[i]] = Math.floor(_gameStats / modulos[i]);
        _gameStats = _gameStats - modulos[i] * w[payoutsWithModulos[i]];
        _tokensWon =
          _tokensWon - payoutsWithModulos[i] * w[payoutsWithModulos[i]];
        returnString +=
          w[payoutsWithModulos[i]] > 0
            ? w[payoutsWithModulos[i]] + "x " + payoutsWithModulos[i] + " won, "
            : "";
      }

      w[125] = Math.floor(_tokensWon / 125);
      returnString += w[125] > 0 ? w[125] + "x " + 125 + " won " : "";

      console.log(w);

      if (_tokensWon - w[125] * 125 !== 0) {
        console.log("Calculation did not work");
        console.log("Rest", _tokensWon - w[125] * 125);
      } else {
        console.log("Calculation worked");
      }

      console.log(returnString);

      this.buildWinningsArr(w, _cards2Play);
    },
  },

  mounted: function () {
    console.log("Play Cards mounted");
    let _cards2Play = this.cards2Play;
    let _tokensWon = this.tokensWon;
    let _gameStats = this.gameStats;

    for (let i = 0; i <= 9; ++i) {
      this.imgArr.push("images/cards/svc.png");
    }

    this.extractGameInfo(_cards2Play, _tokensWon, _gameStats);
  },
};
</script>

