<script>
import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";

export default {
  data() {
    return {
      showTokenomics: false,
      showReaperville: false,
      showReapervilleRewardTokenEmission: false,
      showOtherProjects: false,

      contracts: {},

      reaperBchBalance: 0,
      reaperBchPerNft: "0",

      rewardTokenMintAllowance4NewMinter: 10000,
      addressTo: "",

      feedbackMessage: "",

      rewardTokenMintAllowance4Reaper: 0,

      isAdmin: false,

      reaperMintPaused: false,
      reaperStakingPaused: false,

      allowanceToSet: 5000000,
      totalStakedPower: 0,
      baseStakingPower: 0,
      released: 0,
      rewardTokenEmissionPerHour: 0,
      rewardTokenTotalSupply: 0,
      rewardTokenMinted4Reaper: 0,
    };
  },

  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        this.contracts["svc"] = new ethers.Contract(
          window.ca["svc"],
          RewardToken.abi,
          this.signer
        );
        this.contracts["reaper"] = new ethers.Contract(
          window.ca["reaper"],
          Reaperville.abi,
          this.signer
        );
      })
      .then(() => {
        this.contracts["reaper"]
          .showStakerInfo(window.ca["dev"])
          .then((stakerInfo) => {
            this.totalStakedPower = stakerInfo[2];
            this.baseStakingPower = stakerInfo[3];
            this.rewardTokenEmissionPerHour = String(
              Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
                100000
            );
          });

        this.contracts["svc"]
          .remainingMintersAllowance(window.ca["reaper"])
          .then((_allowance) => {
            this.rewardTokenMintAllowance4Reaper = String(_allowance);
            this.rewardTokenMinted4Reaper =
              Math.round(
                (5 * 10 ** 24 - Number(this.rewardTokenMintAllowance4Reaper)) /
                  10 ** 15
              ) /
              10 ** 3;
          });

        this.contracts["svc"].totalSupply().then((_totalSupply) => {
          this.rewardTokenTotalSupply = String(
            Math.round(_totalSupply / 10 ** 15) / 10 ** 3
          );
        });
      });
  },
};
</script>
